body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Nunito', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

form {
    background: #55acf3;
    padding: 1.5em;
    border-radius: 1em;
}

.form-style {
    margin-top: 2em;
}

.error-message {
    margin-top: 1em;
    color: red;
}

.center {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height:100vh;  
}
.shorter-width {
    width: 90%;
    margin-right: 1em;
}

.margin-top {
    margin-top: 2em;
}

.message {
    color: #009c1a;
    font-weight: bold;
    margin-top: .5em;
    margin-bottom: .5em;
}

.center {
    text-align: center;
}
.navbar {
    height: 70px;
    background-color: #f9f9f9;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    background: #55acf3;
  }
  
  .logo {
    font-size: 1.5em;
    color: white;
  }

  .logo-container > img {
    max-height: 200%;
    width: 68px;
    position: static;
    float: none;
  }

  .right-link-container {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
  }
  
  .link-container {
    display: inline-block;
    height: 100%;
    font-family: 'Open Sans';
    color: black;
    text-decoration: none;
    font-weight: 600;
    margin: 0 23px;
  }
  
  .link-container > div {
    display: table;
    height: 100%;
  }
  
  .link-container > div > div {
    height: 100%;
    display: table-cell;
    vertical-align: middle;
  }
  
  .link-container > div {
    border-bottom: 4px solid rgba(0, 0, 0, 0);
  }

